@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 100vh;
    font-family: 'Lato', sans-serif;
    color: rgb(255, 255, 255);
    background-image: linear-gradient(315deg, #c2118d 0%, #ff503c 74%);
    font-size: 20px;
}

h1 {
    text-transform: uppercase;
    text-align: center;
    padding: 10px;
}

input {
    font-size: 16px;
    padding: 5px 10px;
    margin-top: 10px;
    background: none;
    border: none;
    color: #fff;
    border-bottom: 2px solid #ddd;
}

input::-webkit-input-placeholder {
    color: #ddd;
    font-family: 'Montserrat', sans-serif;
}

input:-ms-input-placeholder {
    color: #ddd;
    font-family: 'Montserrat', sans-serif;
}

input::placeholder {
    color: #ddd;
    font-family: 'Montserrat', sans-serif;
}

button {
    padding: 10px;
    margin-left: 20px;
    color: #fff;
    font-weight: bold;
    background: none;
    border-radius: 5px;
    text-transform: uppercase;
    cursor: pointer;
    transition: background-color .3s, color .3s;
}

button:hover {
    color: #333;
    background-color: #ddd;
}

.wrapper {
    width: 800px;
    height: 400px;
    background-color: rgba(0, 0, 0, .8);
    border: 2px solid white;
    border-radius: 25px;
}

.top {
    height: 260px;
}

.bottom {
    height: 140px;
}

.main-info {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    height: 200px;
}

.city-name {
    margin-bottom: 20px;
    margin-left: 40px;
    font-size: 40px;
}

.headings,
.weather-info {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.headings p,
.weather-info p {
    width: 33.3%;
    text-align: center;
}

.headings {
    height: 35%;
    text-transform: uppercase;
    font-weight: bold;
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
}

.weather-info {
    height: 65%;
    font-size: 26px;
}

.photo {
    width: 140px;
    height: 140px;
}

.warning {
    margin-top: 10px;
    font-size: 14px;
    color: tomato;
}
